* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: Arial, Helvetica, sans-serif;
  line-height: 1.4;
}

a {
  color: #333;
  text-decoration: none;
}

.container {
  padding: 0 1rem;
}

.btn {
  display: inline-block;
  border: none;
  background: rgb(5, 56, 65);
  color: #fff;
  height: 40px;
  width: 120px;
  padding: 7px 20px;
  cursor: pointer;
  font-size: 14pt;
}

.btn:hover {
  background: #666;
}

