* {
    box-sizing: border-box;
    /* border-radius: 10px; */
  }
  
  body {
    background: rgba(184, 163, 163, 0.3);
    /* background: linear-gradient(to right, #d3d0da, #a1b8b6); */
  }
  
  .field {
    width: 100%;
    height: 56px;
    border-radius: 4px;
    position: relative;
    background-color: rgba(142, 146, 146, 0.3);
    transition: 0.3s background-color ease-in-out, 0.3s box-shadow ease-in-out;
  }
  
  .field:hover {
    background-color: rgba(120, 196, 206, 0.45);
    box-shadow: 0px 4px 20px 0px rgba(186, 189, 190, 0.05);
  }
  
  .field.active {
    background-color: #ffffff;
    box-shadow: 0px 4px 20px 0px rgba(148, 181, 184, 0.2);
  }
  
  .field.active input {
    padding: 24px 16px 8px 16px;
  }
  
  .field.active input + label {
    top: 4px;
    opacity: 1;
    color: #512da8;
  }
  
  .field.locked {
    pointer-events: none;
  }
  
  .field input {
    width: 100%;
    height: 56px;
    position: relative;
    padding: 0px 16px;
    border: none;
    border-radius: 4px;
    font-family: "Gotham SSm A", "Gotham SSm B", sans-serif;
    font-size: 16px;
    font-weight: 400;
    line-height: normal;
    background-color: transparent;
    color: #282828;
    outline: none;
    box-shadow: 0px 4px 20px 0px transparent;
    transition: 0.3s background-color ease-in-out, 0.3s box-shadow ease-in-out,
      0.1s padding ease-in-out;
    -webkit-appearance: none;
  }
  
  .field input::-webkit-input-placeholder {
    color: rgba(255, 255, 255, 0.8);
  }
  .field input::-moz-placeholder {
    color: rgba(255, 255, 255, 0.8);
  }
  .field input:-ms-input-placeholder {
    color: rgba(255, 255, 255, 0.8);
  }
  .field input:-moz-placeholder {
    color: rgba(255, 255, 255, 0.8);
  }
  
  .field input + label {
    position: absolute;
    top: 24px;
    left: 16px;
    font-family: "Gotham SSm A", "Gotham SSm B", sans-serif;
    font-size: 12px;
    font-weight: 600;
    line-height: 24px;
    color: #ffffff;
    opacity: 0;
    pointer-events: none;
    transition: 0.1s all ease-in-out;
  }
  
  .field input + label.error {
    color: #ec392f;
  }
  
  .field p.predicted {
    position: absolute;
    top: 8px;
    left: 16px;
    font-family: "Gotham SSm A", "Gotham SSm B", sans-serif;
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    color: #e0e0e0;
    opacity: 1;
    pointer-events: none;
  }

  .form-control {
    text-align: center;
    color: black;
    background-color: #b4eaf1;
    height: 30px;
    width: 20%;
    margin: 5px;
    border-radius: 2px;
  }

  .note-control {
    text-align: left;
    color: black;
    background-color: #b4eaf1;
    /* height: 50px;
    width: 100%; */
    margin: 5px;
    border-radius: 0px;
  }

  .form-group {
    background-color: rgb(235, 250, 255);
    border: #282828;
    padding: 10px;
    border-style: groove;
    border-width: 1px;
    border-radius: 0px;
  }

  .checkbox-control {
    /* background-color: #b4eaf1;
    color: #b4eaf1; */
    height: 20px;
    width: 20px;
    margin: 5px;
  }
  